





















export default {
	data() {
		return {
			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			isCanUse: uni.getStorageSync('isCanUse')||true//默认为true
		};
	},
	methods:{
		// 返回
		goback(){
			let currentIndex= uni.getCurrentPages();
			uni.navigateBack({
				delta:currentIndex-1
			})
		},
		//注册
		register(){
			uni.switchTab({  
			    url: '/pages/login/register'  
			});  
		},
		// 
		 saveCode() {
		    let _this = this;
		    uni.showLoading({
		        title: '登录中...'
		    });
		   // 1.wx获取登录用户code
		    uni.login({
		        provider: 'weixin',
		        success: function(loginRes) {
		            let code = loginRes.code;
					//存储登录code
					_this.mPost('/auth/wxLogin',code,"https://dy.gantangerbus.com/da"
					).then(res=>{
						if(res.code==1){
							if(res.data){
								let data = res.data;
								uni.setStorageSync('Authorization', data.authToken);
							}
						}else{
							uni.showToast({
								title:  res.message,
								icon: "none"
							});
						}
						uni.hideLoading();
					}).catch(err=>{
						uni.hideLoading();
					});
		        },
		    });
		},
		//第一授权获取用户信息===》按钮触发
		getuserinfo:function(res){
			console.log(res);
			let _this = this;
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			uni.getUserInfo({
		        provider: 'weixin',
		        success: function(infoRes) {
					console.log(infoRes);
					// 	储存用户信息
					uni.setStorageSync('userData', JSON.stringify(infoRes.userInfo));
					_this.mPost('/auth/wxUserInfo',infoRes.userInfo,"https://dy.gantangerbus.com/da").then(res=>{
						if(res.code=1){
							_this.goback();
							uni.showToast({
									title:"已授权"
							})
						}else{
							uni.showToast({
								title:  res.message,
								icon: "none"
							});
						}
						uni.hideLoading();
					}).catch(err=>{
						console.log(err);
						uni.hideLoading();
					});
		        },
		        fail(res) {}
		    });
		}
	},
	onLoad(){
		this.saveCode()
	}
}
